.navA{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 30px;
    text-decoration: none;
}

.navAHover {
    transform: perspective(1px) translateZ(0);
    transition-property: color;
    transition-duration: 0.3s;
    border-radius: 5px;
    color: orange;
}

.navAHover:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: orange;
    color: black;
    border-style: none;
    transform: scaleX(0);
    transform-origin: 0 50%;
    transition-property: transform;
    transition-duration: 0.3s;
    transition-timing-function: ease-out;
}

.navAHover:hover, .navAHover:focus, .navAHover:active {
    color: black;
    border-radius: 5px;
}

.navAHover:hover:before, .navAHover:focus:before, .navAHover:active:before {
    transform: scaleX(1);
    border-radius: 5px;
}
